<template>
	<div id="orderEditing">
		<ul>
			<li>
				<label>订单编号</label>
				<input  type="tel"  placeholder="请输入订单编号" v-model="order_id"/>
				<!--<span>修改</span>-->
			</li>
			<li>
				<label>真实姓名</label>
				<input  type="text"  placeholder="请输入真实姓名" v-model="real_name"/>
			</li>
			<li>
				<label>支付宝账号</label>
				<input  type="text"  placeholder="请输入支付宝账号" v-model="alipay_account"/>
			</li>
			<li>
				<label>手机号</label>
				<input style="color: #000;" type="tel"  :value="phone" disabled="disabled"/>
			</li>
			<li>
				<label>验证码</label>
				<input  type="tel"  placeholder="请输入验证码" v-model="code"/>
				<div @click="bindOrderSendCode(flagTime)">{{wait_time}}</div>
			</li>
		</ul>
		<button @click="submission">提交</button>
		<p class="describe">填写淘宝订单编号以及支付宝账号，您会在确认收货后24小时之内收到全额退款</p>
	</div>
</template>

<script>
	import { Toast } from 'mint-ui';
	import md5 from 'js-md5';
	export default {
		name:'orderEditing',
		data () {
			return {
				id:'',//判断是填写还是修改订单 0 填写订单  1 查看、修改订单
				phone: '',
				token:'',
				code:'',//手机验证码
				alipay_account:'',//支付宝账号
				real_name:'',//真实姓名
				order_id:'',//订单号
				goods_id:'',
				group_id:'',
				wait_time:'获取验证码',//倒计时
				flagTime:true
			}
		},
		created () {
			this.token = localStorage.getItem("token");
			this.goods_id = this.$route.query.goods_id;
			this.group_id = this.$route.query.group_id;
			this.id = this.$route.query.id;
			this.mountedEvent();
		},
		mounted () {
			document.title = '填写订单编号';
//			this.phoneNum = this.phoneNum.replace(/(\d{3})\d{4}(\d{4})/, '$1****$2')
		},
		updated () {
		},
		methods : {
			mountedEvent () {
				//0 填写订单  1 查看、修改订单
				if(this.id == 1){
					this.modifyOrderPage();//回显页面
				}else if(this.id == 0){
					this.getPhoneBindOrder();//获取手机号
				}
			},
			submission () {
				//提交订单
				//表单判断
				if(this.order_id == '' || this.order_id == null){
					console.log(this.order_id)
					Toast({
					  message: '订单编号不能为空',
					  duration: 1500
					});
					return;
				}else if(this.real_name == '' || this.real_name == null){
					Toast({
					  message: '请输入姓名',
					  duration: 1500
					});
					return;
				}else if(this.alipay_account == '' || this.alipay_account == null){
					Toast({
					  message: '支付宝账号不能为空',
					  duration: 1500
					});
					return;
				}else if(this.code == '' || this.code == null){
					Toast({
					  message: '请输入手机验证码',
					  duration: 1500
					});
					return;
				}else{
					if(this.id == 1){
						this.modifyOrderCode ();
					}else if(this.id == 0) {
						this.bindOrderCheckCode ();
					}
				}
			},
			getPhoneBindOrder () {
				//手机号显示接口
				var _this = this;
				this.$axios({
					url: this.URL.BASEURL + this.URL.getPhoneBindOrder,
					method: 'post',
					headers:{
						token:this.token
					}
				}).then((res) => {
					res = res ? res.data : null;
					if(res && res.errno == 0){
						_this.phone = res.rst.phone
					}
				})
			},
			bindOrderSendCode () {
				//发送手机验证码接口
				if(this.flagTime == false){
					return false;
				}
				var _this = this;
				var sign = '';
				var phone = this.phone;
				var ttl = (new Date()).getTime();
				var arr = ['ttl','phone'];
				arr = arr.sort()
				if(arr[0] == 'phone'){
					sign += 'phone=' + phone + "&ttl=" + ttl + 'cNHWj7pqBSXTi2DS4uvxqMTzuXOk5xvL'
				}else{
					sign += 'ttl=' + ttl + "&phone=" + phone + 'cNHWj7pqBSXTi2DS4uvxqMTzuXOk5xvL'
				}
				sign = md5(sign);
				this.$axios({
					url:this.URL.BASEURL + this.URL.bindOrderSendCode,
					method:"post",
					data:{
						phone:this.phone,
						ttl:ttl,
						sign:sign
					}
				}).then((res) => {
					res = res ? res.data : null;
					if(res && res.errno == 0){
						_this.wait_time = res.rst.wait_time;
						_this.flagTime = false;
						_this.countDownPhone();
					}else{
						Toast({
						  message: res.err,
						  duration: 1500
						});
					}
				})
			},
			countDownPhone () {
				//手机号倒计时
				var _this = this;
				var time = setInterval(function () {
					_this.wait_time --;
					if(_this.wait_time == 0){
						clearInterval(time);
						_this.flagTime = true;
						_this.wait_time = '获取验证码';
					}
				},1000)
			},
			bindOrderCheckCode () {
				//y验证验证码绑定接口
				this.$axios({
					url:this.URL.BASEURL + this.URL.bindOrderCheckCode,
					method:'post',
					headers:{
						token:this.token
					},
					data:{
						phone:(this.phone).trim(),
						code:(this.code).trim(),
						alipay_account:(this.alipay_account).trim(),//支付宝账号
						real_name:(this.real_name).trim(),//真实姓名
						order_id:(this.order_id).trim(),//订单号
						goods_id:this.goods_id,
						group_id:this.group_id
					}
				}).then((res) => {
					res = res ? res.data :null;
					if(res && res.errno == 0){
						Toast({
						  message: res.rst.msg,
						  duration: 1500
						});
						setTimeout(function () {
							history.go(-1)
						},1500)
					}else{
						Toast({
						  message: res.err,
						  duration: 1500
						});
					}
				})
			},
			modifyOrderPage () {
				//编辑回显页面
				var _this = this;
				this.$axios({
					url:this.URL.BASEURL + this.URL.modifyOrderPage,
					method: 'post',
					headers:{
						token: this.token
					},
					data:{
						goods_id:this.goods_id,
						group_id:this.group_id
					}
				}).then((res) => {
					res = res ? res.data : null;
					if(res && res.errno == 0){
						_this.alipay_account = res.rst.data.alipay_account;
						_this.order_id = res.rst.data.order_id;
						_this.real_name = res.rst.data.real_name;
						_this.phone = res.rst.data.phone;
					}
				})
			},
			modifyOrderCode () {
				//修改订单接口
				this.$axios({
					url:this.URL.BASEURL + this.URL.modifyOrderCode,
					method: 'post',
					headers:{
						token: this.token
					},
					data:{
						phone:(this.phone).trim(),
						code:(this.code).trim(),
						alipay_account:(this.alipay_account).trim(),//支付宝账号
						real_name:(this.real_name).trim(),//真实姓名
						order_id:(this.order_id).trim(),//订单号
						goods_id:this.goods_id,
						group_id:this.group_id
					}
				}).then((res) => {
					res = res ? res.data :null;
					if(res && res.errno == 0){
						Toast({
						  message: res.rst.msg,
						  duration: 1500
						});
						setTimeout(function () {
							history.go(-1)
						},1500)
					}else{
						Toast({
						  message: res.err,
						  duration: 1500
						});
					}
				})
			}
		},
//		beforeRouteLeave(to, from, next) {
//	      if (to.path == "/") {
//	        to.meta.keepAlive = false;
//	      } else {
//	        to.meta.keepAlive = true;
//	      }
//	      next();
//	    }
	}
</script>

<style scoped="scoped" lang="scss">
#orderEditing{
	background: #FFFFFF;
	ul{
		margin:0 0.15rem;
		li{
			height: 0.5rem;
			border-bottom: 0.005rem solid #9C9C9E;
			line-height: 0.5rem;
			font-size: 0.15rem;
			color: #333333;
			label{
				display: inline-block;
				width: 1.195rem;
			}
			input{
				color: #666666;
				font-size: 0.15rem;
				background: #FFFFFf;
				border: none;
				-webkit-user-select:text !important;
			}
			/*&:first-of-type{
			    display: flex;
			    justify-content: space-between;
			    align-items: center;
			    span{
			    	font-size: 0.12rem;
			    	color: #333333;
			    }
			}*/
			&:last-of-type{
				display: flex;
				align-items: center;
				justify-content: space-between;
				input{
					width: 1.4rem;
				}
				div{
					width: 0.76rem;
					height: 0.27rem;
					font-size: 0.12rem;
					color: #FB3D38;
					border-radius: 0.05rem;
					border: 0.01rem solid #FB3D38;
					line-height: 0.29rem;
					text-align: center;
				}
			}
		}
	}
}
button{
	width: 2rem;
	height: 0.4rem;
	border-radius: 0.2rem;
	background: #FB3D38;
	border: none;
	font-size: 0.15rem;
	color: #FFFFFF;
	display: block;
	margin: auto ;
	margin-top: 0.62rem;
}
.describe{
	font-size: 0.15rem;
	color: #333333;
	line-height: 0.25rem;
	width: 3.14rem;
	margin: auto;
	margin-top: 0.47rem;
	text-align: center;
}
</style>


